  opacity: 0;
  background-color: rgba(0,0,0,.4);
  transform:translateX(-100%);
  transition: all .3s ease-in-out;
}

.product-card:hover .product-card-info-bottom {
  transform: translateX(0%);
  opacity: 1;
}

